import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-test-trad',
  templateUrl: './test-trad.component.html',
  styleUrls: ['./test-trad.component.css']
})
export class TestTradComponent implements OnInit {

  /**
   * Variable(s) qui pourront être utilisées dans toutes la classe
   */
  public name     : string;
  public firstName: string;

  /**
   * Constructeur
   */
  constructor() { }

  /**
   * Initialisation des variables au moment du chargement de la vue dans le navigateur
   */
  ngOnInit(): void {
    this.name          = 'Blois';
    this.firstName     = 'Loïc';
  }

}
