import {Component, OnInit} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";
import {Linguages} from "./models/linguages";
import {UrlImg} from "./enums/url-img.enum";
import {Linguage} from "./enums/linguage.enum";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{

  /**
   * Variable(s) qui pourront être utilisées dans toutes la classe
   */
  public langTranslate : string[];
  public linguages     : Linguages[];

  /**
   * Constructeur
   * @param translate
   */
  constructor(public translate: TranslateService) {
  }

  /**
   * Initialisation des variables au moment du chargement de la vue dans le navigateur
   */
  ngOnInit(): void {
    this.langTranslate = [Linguage.EN, Linguage.FR];
    this.translate.setDefaultLang(this.langTranslate[0]); // language par défaut = 'en'
    this.linguages = [
      { value       : Linguage.ENGLISH,
        viewValue   : Linguage.ENGLISH,
        flag        : UrlImg.FLAG_ENGLISH
      },
      { value     : Linguage.FRENCH,
        viewValue : Linguage.FRENCH,
        flag      : UrlImg.FLAG_FRENCH
      }
    ];
  }

  /**
   * Traduction effectuée sur la vue seulement
   * @param lang
   */
  public changeLinguage(lang: string) {
    // traduction des éléments à traduire dans toutes l'application
    switch (lang) {
      case Linguage.ENGLISH:
        this.linguages[0].viewValue = Linguage.ENGLISH;
        this.linguages[1].viewValue = Linguage.FRENCH;
        return this.translate.use(this.langTranslate[0]);
        break;
      case Linguage.FRENCH:
        this.linguages[0].viewValue = Linguage.ANGLAIS;
        this.linguages[1].viewValue = Linguage.FRANCAIS;
        return this.translate.use(this.langTranslate[1]);
        break;
    }
  }
}
